@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../resources/fonts/Roboto.woff") format("woff"), url("../resources/fonts/Roboto.woff2") format("woff2"), url("../resources/fonts/Roboto.ttf") format("ttf"), url("../resources/fonts/Roboto.svg") format("svg");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-family);
  font-size: 14px;
  line-height: 21px;
  color: var(--color-white);
  background: var(--color-main);
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
}
.overlay.is-active {
  display: block;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}
@media (max-width: 1750.98px) {
  .container {
    max-width: 1320px;
  }
}
@media (max-width: 1200.98px) {
  .container {
    max-width: 1140px;
  }
}
@media (max-width: 992.98px) {
  .container {
    max-width: 960px;
  }
}
@media (max-width: 768.98px) {
  .container {
    max-width: 720px;
  }
}
@media (max-width: 720.98px) {
  .container {
    max-width: 540px;
  }
}
@media (max-width: 576.98px) {
  .container {
    max-width: 100%;
  }
}

.main-title {
  font-size: 42px;
  font-weight: 700;
  line-height: 51px;
  color: var(--color-white);
}

.title {
  font-size: 42px;
  font-weight: 700;
  line-height: 51px;
  color: var(--color-white);
}

:root {
  --font-family: "Montserrat", sans-serif;
  --content-width: 1750px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --color-main: #131313;
  --color-text: #171717;
  --color-gray: #666666;
  --color-white: #ffffff;
  --grid-gap: 30px;
}

.btn {
  position: relative;
  display: block;
  width: 280px;
  height: 60px;
  font-size: 12px;
  font-weight: 700;
  line-height: 18px;
  color: var(--color-white);
  text-transform: uppercase;
  z-index: 10;
  transition: all 0.3s ease-in-out;
}
.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 95%;
  height: 100%;
  background: #C2AB81;
  transform: skew(-17deg, 0);
  z-index: -1;
}
.btn::after {
  content: "";
  position: absolute;
  top: 0;
  right: 6px;
  width: 4px;
  height: 100%;
  background: var(--color-white);
  transform: skew(-17deg, 0);
  z-index: -1;
  transition: all 0.3s ease-in-out;
}
.btn:hover {
  color: #C2AB81;
}
.btn:hover.btn::after {
  width: 100%;
  right: 0;
}
.btn:active {
  transform: translateY(4px);
}

.burger {
  display: none;
  height: 22px;
  cursor: pointer;
  z-index: 10;
}
@media (max-width: 1749.98px) {
  .burger {
    display: block;
  }
}
.burger__line {
  position: relative;
  width: 30px;
  border: 1px solid var(--color-white);
  border-radius: 15px;
  transform: translateY(12px);
}
.burger__line::before, .burger__line::after {
  content: "";
  position: absolute;
  left: -1px;
  border: 1px solid var(--color-white);
  border-radius: 15px;
  background: var(--color-white);
  transition: width 0.3s ease-in-out;
}
.burger__line::before {
  top: -8px;
  width: 15px;
}
.burger__line::after {
  bottom: -8px;
  width: 22px;
}
.burger__line.is-active::before, .burger__line.is-active::after {
  width: 30px;
}

.input {
  width: 100%;
  height: 50px;
  padding: 15px;
  border: 1px solid var(--color-gray);
  border-radius: 5px;
  font-family: "Mulish", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: 1.5px;
  color: var(--color-text);
}
.input.correct {
  border-color: #198754;
  box-shadow: 0 0 0 0.25rem rgba(25, 135, 84, 0.25);
}
.input.inCorrect {
  border-color: #dc3545;
  box-shadow: 0 0 0 0.25rem rgba(220, 53, 69, 0.25);
}

.nav {
  z-index: 100;
}
@media (max-width: 1749.98px) {
  .nav {
    position: absolute;
    top: 122px;
    left: 0;
    width: 50%;
    padding-left: 50px;
    padding-bottom: 50px;
    background: var(--color-main);
    transform: translateX(-110%);
    opacity: 0;
    transition: all 0.3s ease-in-out;
  }
}
@media (max-width: 992.98px) {
  .nav {
    width: 100%;
    height: 100vh;
  }
}
@media (max-width: 480.98px) {
  .nav {
    padding: 0 15px 21px;
  }
}
.nav.is-active {
  transform: none;
  opacity: 1;
}
.nav-list {
  display: flex;
}
@media (max-width: 1749.98px) {
  .nav-list {
    flex-direction: column;
  }
}
.nav-item:not(:last-child) {
  margin-right: 42px;
}
@media (max-width: 1749.98px) {
  .nav-item {
    margin-right: 0;
  }
  .nav-item:not(:last-child) {
    margin-bottom: 15px;
  }
}
@media (max-width: 1749.98px) and (max-width: 720.98px) {
  .nav-item:last-child {
    margin-bottom: 51px;
  }
}
.nav-item__link {
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
  color: var(--color-white);
  text-transform: uppercase;
  transition: color 0.2s ease-in-out;
}
.nav-item__link:hover {
  color: #C2AB81;
}
@media (max-width: 1749.98px) {
  .nav-item__link {
    font-size: 36px;
    font-weight: 700;
    line-height: 54px;
    color: transparent;
    -webkit-text-stroke: 1px var(--color-gray);
  }
}
@media (max-width: 720.98px) {
  .nav-item__link {
    font-size: 20px;
    line-height: 30px;
  }
}

.card {
  width: 320px;
  display: flex;
  flex-direction: column;
  background: var(--color-white);
}
@media (max-width: 768.98px) {
  .card {
    width: 334px;
  }
}
@media (max-width: 720.98px) {
  .card {
    width: 100%;
  }
}
.card__header img {
  width: 100%;
}
.card__body {
  height: 100%;
  padding: 25px 24px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card__heading {
  margin-bottom: 20px;
  font-family: "Mulish", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: 1.5px;
  color: var(--color-text);
  text-transform: uppercase;
}
.card__descr {
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  color: var(--color-gray);
}
.card__descr_bold {
  font-size: 16px;
  line-height: 26px;
  color: var(--color-text);
}
.card__posted {
  margin-bottom: 25px;
  font-size: 12px;
  font-weight: 400;
  line-height: 24px;
  color: var(--color-gray);
}
.card__posted_date {
  font-family: "Mulish", sans-serif;
}
.card__btn {
  width: 100%;
  height: 50px;
  background: var(--color-text);
  font-family: "Mulish", sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  color: var(--color-white);
  transition: background 0.3s ease-in-out;
}
.card__btn:hover {
  background: #2b2a2a;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  display: none;
  max-width: 400px;
  width: 100%;
  background: var(--color-white);
  border-radius: 5px;
  transform: translate(-50%, -50%);
  animation: smoothVisible 0.3s ease-in-out;
  z-index: 100;
}
@media (max-width: 480px) {
  .modal {
    max-width: 300px;
  }
}
.modal.is-active {
  display: block;
}
.modal-wrapper {
  position: relative;
  padding: 40px 20px;
}
.modal__close {
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 20px;
  color: var(--color-text);
  cursor: pointer;
}
.modal__title {
  margin-bottom: 20px;
  color: var(--color-text);
  text-align: center;
}

@keyframes smoothVisible {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.form-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.form__input {
  margin-bottom: 20px;
}
.form__btn {
  width: 100%;
  height: 50px;
  background: var(--color-text);
  border-radius: 5px;
  font-family: "Mulish", sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  color: var(--color-white);
  transition: background 0.3s ease-in-out;
}
.form__btn:hover {
  background: #2b2a2a;
}

@media (max-width: 1749.98px) {
  .header {
    position: relative;
  }
}
.header-wrapper {
  display: flex;
  align-items: center;
  padding: 28px 0;
}
.header-burger {
  margin-right: auto;
}
.header-logo {
  margin-right: 76px;
}
@media (max-width: 1749.98px) {
  .header-logo {
    margin-right: 0;
  }
}
.header-feedback {
  display: flex;
  align-items: center;
  margin-left: auto;
}
@media (max-width: 720.98px) {
  .header-feedback {
    display: none;
  }
}
.header-feedback_mobile {
  display: none;
}
@media (max-width: 720.98px) {
  .header-feedback_mobile {
    display: block;
  }
}
.header-feedback__phone {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  padding: 0 36px;
  opacity: 0.5;
  overflow: hidden;
  transition: opacity 0.2s ease-in-out;
}
@media (max-width: 720.98px) {
  .header-feedback__phone {
    width: 330px;
    margin-bottom: 10px;
  }
}
@media (max-width: 480.98px) {
  .header-feedback__phone {
    width: 320px;
    margin: 0 auto 10px;
  }
}
@media (max-width: 320.98px) {
  .header-feedback__phone {
    width: 260px;
  }
}
.header-feedback__phone::before {
  content: "";
  position: absolute;
  width: 80%;
  height: 100%;
  border: 1px solid var(--color-white);
  transform: skew(-17deg, 0);
  transition: border-color 0.3s ease-in-out;
}
@media (max-width: 720.98px) {
  .header-feedback__phone::before {
    transform: skew(-20deg, 0);
  }
}
@media (max-width: 480.98px) {
  .header-feedback__phone::before {
    width: 93%;
  }
}
@media (max-width: 320.98px) {
  .header-feedback__phone::before {
    width: 90%;
  }
}
.header-feedback__phone::after {
  content: "";
  position: absolute;
  top: 0;
  right: 10px;
  width: 0;
  height: 100%;
  background: #C2AB81;
  transform: skew(-17deg, 0);
  z-index: -1;
  transition: width 0.2s ease-in-out;
}
@media (max-width: 720.98px) {
  .header-feedback__phone::after {
    right: 32px;
    transform: skew(-20deg, 0);
  }
}
.header-feedback__phone:hover {
  opacity: 1;
}
.header-feedback__phone:hover.header-feedback__phone::before {
  border-color: transparent;
}
.header-feedback__phone:hover.header-feedback__phone::after {
  width: 79%;
}
@media (max-width: 720.98px) {
  .header-feedback__phone:hover.header-feedback__phone::after {
    width: 80%;
  }
}
@media (max-width: 480.98px) {
  .header-feedback__phone:hover.header-feedback__phone::after {
    width: 93%;
    right: 10px;
  }
}
@media (max-width: 320.98px) {
  .header-feedback__phone:hover.header-feedback__phone::after {
    width: 90%;
    right: 12px;
  }
}
.header-feedback__btn {
  margin-left: 20px;
}
@media (max-width: 720.98px) {
  .header-feedback__btn {
    margin-left: 0;
  }
  .header-feedback__btn::before {
    transform: skew(-20deg, 0);
  }
  .header-feedback__btn::after {
    right: 0;
    width: 0;
    transform: skew(-20deg, 0);
  }
  .header-feedback__btn:hover.header-feedback__btn::after {
    right: 16px;
    width: 95%;
  }
}
@media (max-width: 720.98px) and (max-width: 320.98px) {
  .header-feedback__btn:hover.header-feedback__btn::after {
    right: 14px;
  }
}
@media (max-width: 480.98px) {
  .header-feedback__btn {
    width: 320px;
    margin: 0 auto;
  }
}
@media (max-width: 320.98px) {
  .header-feedback__btn {
    width: 260px;
  }
}

.hero {
  position: relative;
  background-image: url("../resources/images/hero-bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 60px;
}
.hero-wrapper {
  padding-top: 600px;
  padding-bottom: 100px;
}
@media (max-width: 1320.98px) {
  .hero-wrapper {
    padding-bottom: 0;
  }
}
@media (max-width: 768.98px) {
  .hero-wrapper {
    padding-top: 551px;
    background-position: center;
  }
}
@media (max-width: 720.98px) {
  .hero-wrapper {
    padding-top: 266px;
  }
}
.hero-title {
  margin-bottom: 40px;
}
@media (max-width: 720.98px) {
  .hero-title {
    font-size: 20px;
    line-height: 24px;
  }
}
.hero-descr {
  margin-bottom: 40px;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: var(--color-white);
}

.cards {
  padding-top: 134px;
  padding-bottom: 256px;
}
@media (max-width: 768.98px) {
  .cards {
    padding-bottom: 156px;
  }
}
.cards-title {
  margin-bottom: 53px;
}
.cards-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 30px;
  gap: 30px;
}
@media (max-width: 768.98px) {
  .cards-list {
    gap: 24px 20px;
  }
}
@media (max-width: 720.98px) {
  .cards-list {
    gap: 0;
    justify-content: center;
  }
}
@media (max-width: 720.98px) {
  .cards-item:not(:last-child) {
    margin-bottom: 20px;
  }
}
.cards__btn {
  margin: 0 auto;
}